@import '../../buttons/button-with-border-gradient/button-with-border-gradient';
@import '../../buttons/button-with-purple-gradient-and-arrow/button-with-purple-gradient-and-arrow';
@import '../../text-field-default/text-field-default';
@import '../../radio-button/radio-button';
@import '../../toggle-button/toggle-button';

.registration {
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  max-width: 27.1429rem;
  padding: 2.8571rem 2.1429rem 2.1429rem;
  background: $white-color-100;
  border: 0.0714rem solid $black-color-12;
  border-radius: 0.2857rem;
  box-shadow: 0rem 0rem 1.7857rem $black-color-20;

  &__title {
    margin: 0 0 1.4286rem -0.1429rem;
    font-weight: bold;
    font-size: 1.7143rem;
    line-height: 2.1429rem;
    color: $primary-color-100;
    letter-spacing: -0.0357rem;
  }

  &__form {
    margin-bottom: 2.1143rem;
  }

  &__group {
    margin: 0;
    padding: 0;
    border: none;

    &:nth-child(1) {
      margin-bottom: 0.5143rem;

      .registration__item:nth-child(1) {
        margin-bottom: 0.7143rem;
      }

      .registration__item:nth-child(2) {
        margin-bottom: 0.5714rem;
      }
    }

    &:nth-child(2) {
      margin-bottom: 1.5714rem;
    }

    &:nth-child(3) {
      margin-bottom: 0.5286rem;

      .registration__item:nth-of-type(1) {
        margin-bottom: 0.7857rem;
      }
    }

    &:nth-child(4) {
      margin-bottom: 1.5714rem;
    }
  }

  &__label,
  &__group-title {
    font-weight: bold;
    font-size: 0.8571rem;
    line-height: 1.0714rem;
    color: $primary-color-100;
    text-transform: uppercase;
  }

  &__group-title {
    margin: 0 0 0.3571rem;
  }

  &__item {
    margin: 0;
  }

  &__field {
    width: 100%;
    padding: 0.9286rem 1.0714rem;
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none;

    .registration__item + .registration__item {
      margin-left: 1.5rem;
    }
  }

  &__login {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    min-width: 10rem;
    max-width: 15.1429rem;
    margin-right: 0.7143rem;
    line-height: 1.2857rem;
    color: $black-color-100;
  }

  .button_theme_with-purple-gradient-and-arrow {
    width: 100%;
  }

  .button__text {
    padding: 0.9286rem 1.6429rem;
  }
}
